export default function Icon({ w, h, color, rotate, loc, dx, dy }) {
  const distanceX = dx;
  const distanceY = dy;
  const style = () => {
    if (loc === "left") {
      console.log(distanceX);
      return {
        position: "absolute",
        top: { distanceX },
        left: { distanceY }
      };
    }
    if (loc === "right") {
      return {
        position: "absolute",
        bottom: { dx },
        right: { dy }
      };
    }
  };

  return (
    <svg width={w} height={h} viewBox="0 0 100 100" style={style()}>
      <path
        fill="none"
        transform={rotate}
        style={{
          WebkitTransformOrigin: "50%50%",
          MsTransformOrigin: "50%50%",
          transformOrigin: "50%50%"
        }}
        stroke={color}
        strokeWidth="8"
        d="M10 10L90 10 90 90"
      ></path>
    </svg>
  );
}
